  },
  data() {
    return {

      resultColumnList:[], //æ³¨æ„ï¼Œé»˜è®¤å€¼è¿˜æ˜¯è¦çš„ï¼Œè¦ä¸ç„¶å°±ä¼šæŠ¥é”™ã€‚

      //é‡ç½®ç”¨
      //é€šè¿‡å­ç»„ä»¶å‘é€äº‹ä»¶ï¼Œåœ¨æœ¬ç»„ä»¶ä¸­ï¼ŒæŽ§åˆ¶v-ifæ¥æ˜¾ç¤ºéšè—çš„å˜åŒ–ï¼Œä»Žè€Œå®Œæˆç»„ä»¶çš„é‡æ–°åŠ è½½ï¼1
      hackReset:true,

      items:[{
        span: 12,
        label: "å§“å",
        keyName: 'è¿™ä¸ªæ˜¯ç”¨æ¥ç»‘å®šçš„',
        type: 'beeInput',
        placeholder: 'è¯·è¾“å…¥',
        value: '',
        rule: {},
      },{
        span: 12,
        label: "å–œçˆ±",
        keyName: 'è¿™ä¸ªæ˜¯ç”¨æ¥ç»‘å®šçš„',
        type: 'beeSelect',
        placeholder: 'è¯·è¾“å…¥',
        value: '',
        rule: {},
      },{
        span: 12,
        label: "é€‰æ‹©æ—¥æœŸ",
        keyName: 'è¿™ä¸ªæ˜¯ç”¨æ¥ç»‘å®šçš„',
        type: 'beeDatePicker',
        placeholder: 'æ—¥æœŸé€‰æ‹©',
        value: '',
        rule: {},
      },],
      tableData:[],
      currentSearchOptions:{},
