<template>

  <div class="conditionBox">
    <el-row :gutter="10">
      <component
      v-for="(item, index) in items"
      :key="index"
      :is="item.type"
      :item="item"
      :ref="item.keyName"
      :labelPosition="store.state.store_labelPosition"
      :colSpan="store.state.store_colSpan"
      @sonChange = "sonChangeHandle"
      ></component>
    </el-row>
    <div class="funBox">

      <template v-for="one in resetBtn">
        <el-button 
          :key= "one.text" 
          @click="resetForm('ruleForm')" 
          :disabled="resetBtnDisabled"
        >
          {{one.text}}
        </el-button>
      </template>

      <template v-for="(one,index) in searchBtns">
        <el-button 
        ref = "searchBrn"
        :key= "index" 
        type= "primary" 
        :diyCoreCode= "one.diyCoreCode" 
        @click="submitForm(one.diyCoreCode,one.dataUrl,one.resultColumnList)"
        >
          {{one.text}}
        </el-button>
      </template>
    </div>
  </div>
</template>

<script>
import axios from "axios";

import beeInput from './beeInput.vue';
import beeInputRange from './beeInputRange.vue';
import beeDatePicker from './beeDatePicker.vue';
import beeDatePickerRange2 from './beeDatePickerRange2.vue';
import beeSelect from './beeSelect.vue';
import { setTimeout } from 'timers';

import store from '@/src/store';
import bee from '@/src/tools/bee.js';

export default {
  name: "beeTitle",
  props: {
    items:Array,
    searchBtns:null,
    resetBtn:null,
    currentPage:null,
    pageSize:null,
    showPage:null,
  },
  data() {
    return {
      store,
      conditionData:{}, //è¿™ä¸ªç”¨æ¥ä¿å­˜æŸ¥è¯¢æ¡ä»¶çš„æœ€åŽç»“æžœ
      resetBtnDisabled:true, //é»˜è®¤ä¸€å¼€å§‹ä¸è®©ä½¿ç”¨resetï¼Œåªæœ‰ä¸€å®šå»¶æ—¶åŽæ‰èƒ½ç”¨ã€‚
    };
  },
  methods:{
    //åˆå§‹åŒ–æŸ¥è¯¢æ¡ä»¶çš„å€¼
    initConditionData(arr){

      //æ·±åº¦æ‹·è´ã€‚å¦è€…åŽŸå¯¹è±¡ä¼šè¢«æ”¹å˜ã€‚
      arr = JSON.parse(JSON.stringify(arr))

      let newArr={};
      for(let i=0;i<arr.length;i++){

        //å¯¹æ—¶é—´èŒƒå›´çš„ç»„ä»¶çš„é»˜è®¤å€¼åšå¤„ç†
        let defaultValue = arr[i].defaultValue;
        if(arr[i].type==='beeDatePickerRange2' && defaultValue){
          //å¦‚æžœé»˜è®¤å€¼å­˜åœ¨
          if(Array.isArray(defaultValue)){
            let a = defaultValue.map(function(one){
              return new Date(one).getTime();
            }).join('-');

            arr[i].defaultValue =a;
          }
        }

        //ä¸ä¸ºç©ºçš„æ—¶å€™æ‰æ·»åŠ ï¼Œå¦åˆ™å°±ä¼šæŸ¥è¯¢ä¸ºç©ºå­—ç¬¦ä¸²çš„æƒ…å†µ
        if(arr[i].defaultValue!==''){
          newArr[arr[i].keyName] = (arr[i].defaultValue);
        }
      }
      console.log('æ¡ä»¶æœç´¢åˆå§‹æ•°æ®===>')
      console.log(newArr)
      return newArr;
    },

    submitForm(code,url,resultColumnList){
      //console.log("æ¡ä»¶æŸ¥è¯¢æœ€ç»ˆæ•°æ®")
      //console.log(this.conditionData);
      
      // console.log(code)
      // console.log(url)
      // console.log(this.currentPage)
      // console.log(this.pageSize)


      let abc = bee.effectiveKeys(this.conditionData);
      let body = Object.assign({diyCoreCode:code},abc);

      //å¦‚æžœéœ€è¦æ˜¾ç¤ºåˆ†é¡µï¼Œè¦å¸¦ä¸Šè¿™ä¸¤ä¸ªå‚æ•°
      if(this.showPage){
        body.currentPage = store.state.store_currentPage || this.currentPage;
        body.pageSize = this.pageSize
        //èŽ·å–æ•°æ®æº
        axios.post(url,body).then(response => {
          let tableData = response.data.data.recordList;
          let totalPage = response.data.data.recordCount;
          this.$emit('tableDataOK', tableData, this.conditionData,code,url,totalPage,resultColumnList); 
        });
      }else{
        //èŽ·å–æ•°æ®æº
        axios.post(url,body).then(response => {
          let tableData = response.data.data;
          let totalPage = -1;
          this.$emit('tableDataOK', tableData, this.conditionData,code,url,totalPage,resultColumnList); 
        });
      }


    },
    resetForm(){
      this.$emit('reset');
    },
    //æŽ¥å—å­ç»„ä»¶ä¸­å€¼çš„å˜åŒ–ï¼Œæ›´æ–°æ•°æ®
    sonChangeHandle(v,item){
      this.conditionData[item.keyName] = v;
    }
  },
  computed: {
  },
  components:{
    beeInput,
    beeInputRange,
    beeDatePicker,
    beeSelect,
    beeDatePickerRange2,
  },
  mounted(){
    //console.log(this.items)
    //console.log(this.searchBtns)
    //console.log(this.$refs['searchBrn'][0])
    
    //å»¶æ—¶å¤„ç†ï¼Œç­‰æŸ¥è¯¢æ¡ä»¶å®Œæˆæ¸²æŸ“ã€‚å†æ¨¡æ‹Ÿç‚¹å‡»
    // setTimeout(()=>{
    //   this.$refs['searchBrn'][0].$el.click();
    // },1000)

    setTimeout(()=>{
      this.resetBtnDisabled = false;
    },1000)
  },
  //è¿™ä¸ªä¸€å®šè¦åŠ ï¼Œå¦è€…â€é‡ç½®â€œçš„æ—¶å€™ï¼Œå°±ä¼šå‡ºçŽ°æŸ¥è¯¢æ¡ä»¶æ²¡æœ‰å¸¦ä¸Šçš„bug
  created(){
    //åˆå§‹åŒ–æŸ¥è¯¢æ¡ä»¶çš„å€¼
    this.conditionData = this.initConditionData(this.items);
  },
  updated(){
    //åˆå§‹åŒ–æŸ¥è¯¢æ¡ä»¶çš„å€¼
    this.conditionData = this.initConditionData(this.items);
  },
  watch:{
  }
  
};
</script>

